.main-nav {
  width: 700px;
  margin-top: 10px;

  @media (max-width: 992px) {
    position: absolute;
    right: 0;
    left: 0;
    top: 380px;
    width: auto;
    background-color: #ffffff;
    margin-top: 0;
  }
}

.main-nav__list {
  width: 100%;
  padding-left: 0;
  margin: 0;
  display: flex;
  justify-content: space-between;
  list-style: none;
  font-size: 14px;
  font-weight: 400;
  line-height: 36px;

  @media (max-width: 992px) {
    flex-direction: column;
    align-items: center;
    padding-bottom: 40px;
  }
}

.main-nav__item {
  display: flex;
  align-items: center;
  height: 100%;

  @media (max-width: 992px) {
    flex-direction: column;
  }

  &:not(:last-child) {
    flex: auto;

    &::after {
      content: "";
      display: inline-flex;
      width: 6px;
      height: 6px;
      margin-right: auto;
      margin-left: auto;
      background-color: #00091c;
      border-radius: 50%;
    }
  }
}

.main-nav__link {
  color: #00091c;
  text-decoration: none;

  @media (max-width: 992px) {
    line-height: 46px;
  }

  @media (min-width: 993px) {
    &:hover,
    &:focus {
      color: #306ae0;
    }

    &:active {
      opacity: 0.7;
    }
  }

  &_active {
    color: #306ae0;
  }
}


