.page-title {
  font-weight: 500;
  font-size: 48px;
  line-height: 58px;
  letter-spacing: 1.2px;
  color: @text-color_default;

  .container();
}


@media screen and (max-width: 1024px) {
  .page-title {
    font-size: 32px;
    line-height: 48px;
  }
}

@media screen and (max-width: 768px) {
  .page-title {
    padding-left: 40px;
    padding-right: 40px;
  }
}

@media screen and (max-width: 660px) {
  .page-title {
    font-size: 28px;
    line-height: 42px;
  }
}

@media screen and (max-width: 425px) {
  .page-title {
    padding-left: 20px;
    padding-right: 20px;
  }
}
