.col-1-1 {
  flex-basis: 0;
  max-width: 100%;
  flex-grow: 1;
}

.col-1-2 {
  position: relative;
  box-sizing: border-box;
  flex-basis: 50%;
}

.col {
  &_align-right {
    display: flex;
    justify-content: flex-end;
  }

  &_align-left {
    display: flex;
    justify-content: flex-start;
  }
}

@media screen and (max-width: 768px) {
  .col_align-right {
    justify-content: flex-start;
  }

  .col_order-first {
    order: 1;
  }

  .col_order-second {
    order: 2;
  }
}
