.index-parents {
  display: flex;
  flex-direction: column;
}

.index-parents__col_with-bg {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    background-image: url("../img/bg-index-parents.svg");
    background-repeat: no-repeat;
    display: block;
    width: 109.7%;
    height: 264.65%;
    bottom: -7.51%;
    left: 52.94%;
    z-index: -1;
  }
}

.index-parents__large-image {
  position: relative;
  width: 50%;
  box-sizing: border-box;
  padding-top: 50px;
  padding-right: 56px;

  img {
    position: relative;
    display: inline-block;
    width: 100%;
    object-fit: contain;
    object-position: top center;
    height: auto;
    z-index: 10;
  }
}

.index-parents__main-content {
  padding-right: 34px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex-basis: 50%;
}

.index-parents__section-title {
  margin-top: 125px;
}

.index-parents__row {
  padding-left: 25px;
  padding-right: 25px;
  box-sizing: border-box;
  padding-top: 130px;
}

@media screen and (max-width: 1024px) {
  .index-parents__large-image {
    padding-top: 30px;
    padding-right: 20px;
    flex: none;
  }

  .index-parents__section-title {
    margin-top: 50px;
    margin-bottom: 25px;
  }

  .index-parents__lead {
    margin-bottom: 32px;
  }

  .index-parents__text {
    margin-bottom: 29px;
  }

  .index-parents__col_with-bg::after {
    display: none;
  }
}

@media screen and (max-width: 768px) {
  .index-parents__main-content {
    padding-left: 40px;
    padding-right: 40px;
  }
}

@media screen and (max-width: 660px) {
  .index-parents__large-image {
    width: 100%;
    margin-left: -20px;
    align-self: flex-start;
  }

  .index-parents__main-content {
    padding-left: 20px;
    padding-right: 20px;
    flex-basis: auto;
  }

  .index-parents__section-title {
    margin-top: 0;
    margin-bottom: 0;
  }

  .index-parents__lead {
    margin-bottom: 22px;
  }

  .index-parents__text {
    margin-bottom: 14px;
  }

  .index-parents__text_last-child {
    margin-bottom: 24px;
  }

  .index-parents__row {
    padding-top: 17%;
    padding-left: 20px;
    padding-right: 20px;

    .col-1-2:first-child {
      order: 2;
      margin-bottom: 10px;
    }

    .col-1-2:last-child {
      order: 1;
    }

    &_fluid {
      align-items: center;
    }
  }
}
