.section-image__primary {
  width: 100%;
  height: 0;
  padding-top: 100%;
  position: relative;
  overflow: hidden;
  border-radius: 50%;
  box-sizing: content-box;
  border: 2px solid @border-color-light;
  background-color: @bg-color_grey;

  img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: auto;
  }
}

.section-image__secondary {
  /// пропорции из макета
  width: 64%;
  padding-top: 64%;
  border: 2px solid @border-color-light;
  border-radius: 50%;
  overflow: hidden;
  position: absolute;
  background-color: @bg-color_grey;

  img {
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    height: auto;
    position: absolute;
    width: 100%;
  }

  &_position-left-center {
    top: 8.63%;
    left: -50.35%;
  }

  &_position-right-center {
    top: 46px;
    right: -50.35%;
  }

  &_position-left-top {
    top: -68px;
    left: -61.7%;
  }

  &_position-right-baseline {
    top: 0;
    right: -61.7%;
  }
}

.section-image__text {
  position: absolute;
  margin: 0;
  width: 100%;
  top: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;
  font-size: 1.57vw;
  font-weight: 500;
  line-height: 2.08vw;
  letter-spacing: 0.75px;
  padding: 1.7vw;
  box-sizing: border-box;
  text-align: center;
  color: @text-color_light;
}

.section-image__lead {
  font-size: 1.6em;
  line-height: 1.6em;
  letter-spacing: 0.04em;
}

.section-image__description {
  width: 100%;
}

.section-image {
  display: inline-block;
  position: relative;
  /// пропорция из макета
  width: 59.26%;
  height: 93.9%;

  &_bg-red {
    background-color: @bg-color_red;
  }

  &_bg-green {
    background-color: @bg-color_green;
  }

  &_bg-orange {
    background-color: @bg-color_orange;
  }

  &_bg-blue {
    background-color: @bg-color_blue;
  }
}

@media screen and (max-width: 768px) {
  .section-image {
    &_align-right {
      margin-left: 29.6%;
    }
  }

  .section-image__text {
    font-size: 3vw;
    line-height: 4.78vw;
  }
}

