.breadcrumb {
  display: flex;
  margin-top: 35px;
  list-style-type: none;

  .container();
}

.breadcrumb__item {
  position: relative;

  &:not(:last-child) {
    margin-right: 14px;

    &::after {
      content: "/";
      position: absolute;
      bottom: 0;
      right: -12px;
    }
  }
}

.breadcrumb__link {
  color: @text-color_default;
  text-decoration: none;
}

@media screen and (min-width: @desktop) {
  .breadcrumb__link {
    &:hover,
    &:focus {
      color: @text-color_accent;
    }

    &:active {
      color: darken(@bg-color_accent, 10%);
    }
  }
}

@media screen and (max-width: 768px) {
  .breadcrumb {
    padding-left: 40px;
    padding-right: 40px;
  }
}

@media screen and (max-width: 425px) {
  .breadcrumb {
    padding-left: 20px;
    padding-right: 20px;
  }
}
