.selectric {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 448px;
  height: 60px;
  padding-left: 25px;
  padding-right: 25px;
  box-sizing: border-box;
  font-size: @text-size_default;
  border-color: @border-color-grey;
  background-color: transparent;

  .font-family();

  .icon__arrow-right {
    transform: rotate(0);
    transition: transform 0.1s ease;
  }
}

.selectric .label {
  margin-left: 0;
  font-size: @text-size_default;

  .font-family();
}

.selectric-open {
  .icon__arrow-right {
    transform: rotate(90deg);
    transition: transform 0.1s ease;
  }
}

.selectric-items ul {
  height: 340px;
  //overflow: hidden;
}

.selectric-items ul,
.selectric-items li {
  min-height: 60px;
  font-size: @text-size_default;
}

.selectric-items {
  background-color: @bg-color_light;
  border-color: @border-color-grey;
  box-shadow: none;
}

.selectric-items li {
  display: flex;
  align-items: center;
  padding-left: 25px;
  padding-right: 25px;
  box-sizing: border-box;
  color: @text-color_default;
  line-height: 40px;

  &:not(:last-child) {
    border-bottom: 1px solid @border-color-grey;
  }

  &:hover {
    background-color: @bg-color_green;
    color: @text-color_light;
  }
}

.selectric-items li.highlighted {
  background-color: #f0f0f0;

  &:hover {
    background-color: @bg-color_green;
  }
}

@media screen and (max-width: 768px) {
  .selectric,
  .selectric-wrapper {
    width: 100%;
  }
}
