.index-kids {
  position: relative;
  padding-top: 180px;
  background-image: url("../img/bg-index-kids.svg");
  background-repeat: no-repeat;
  background-position: center top 5%;
  background-size: auto;

  .container {
    padding-left: 25px;
    padding-right: 25px;
  }
}

.index-kids__section-title {
  margin-top: 50px;
}

.index-kids__text {
  max-width: 450px;
  margin-top: 0;
  margin-bottom: 40px;
}

.index-kids__video {
  position: relative;
  width: 66%;
  z-index: 10;
  margin-top: 180px;
  padding-bottom: 100px;
}

.index-kids__inner {
  position: relative;
  overflow: hidden;
  padding-top: 56.25%;
}

.index-kids__inner iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.index-kids__video-bg {
  background-image: url("../img/index-kids-mermaid.png");
  background-repeat: no-repeat;
  background-position: right top 160px;
  background-size: 30%;
  z-index: 5;
}

.index-kids__hook {
  position: absolute;
  bottom: 0;
  right: 0;
  height: 760px;
  width: 910px;
}

@media screen and (max-width: 1600px) {
  .index-kids {
    background-size: auto, 125%;
  }
}

@media screen and (max-width: 1450px) {
  .index-kids {
    background-size: auto, 138%;
  }
}

@media screen and (max-width: 1366px) {
  .index-kids {
    background-size: auto, 150%;
  }
}

@media screen and (max-width: 1280px) {
  .index-kids {
    background-size: auto, 165%;
  }
}

@media screen and (max-width: 1200px) {
  .index-kids {
    padding-top: 110px;
    background-position: center top -35%, center bottom;
  }

  .index-kids__section-title {
    margin-top: 0;
  }

  .index-kids__section-image {
    margin-top: 50px;
  }
}

@media screen and (max-width: 1024px) {
  .index-kids {
    background-position: center top, center bottom;
    background-size: 1024px;
  }

  .index-kids__video {
    margin-top: 110px;
  }

  .index-kids__video-bg {
    background-position: right top 110px;
  }
}

@media screen and (max-width: 768px) {
  .index-kids__video-bg {
    background-image: none;
  }

  .index-kids__video {
    margin-top: 60px;
    width: 100%;
  }
}

@media screen and (max-width: 660px) {
  .index-kids_order_second {
    order: 2;
  }

  .index-kids_order_first {
    order: 1;
  }

  .index-kids {
    padding-top: 0;
    padding-left: 20px;
    padding-right: 20px;
  }

  .index-kids__section-title {
    margin-top: 20px;
  }

  .index-kids__text {
    margin-bottom: 14px;

    &_last-child {
      padding-bottom: 16px;
    }
  }

  .index-kids .container {
    padding-left: 0;
    padding-right: 0;
  }

  .index-kids .col-1-2 {
    padding-left: 0;
    padding-right: 0;
  }

  .index-kids__video-bg {
    background: none;
  }
}
