.page-header {
  position: relative;
  max-width: 1410px;
  margin-right: auto;
  margin-left: auto;
  padding-top: 20px;
  padding-right: 25px;
  padding-left: 25px;
  box-sizing: border-box;
  z-index: 100;

  @media (max-width: 992px) {
    background-color: #ffffff;
    height: 50px;
    padding-top: 0;

    &.active {
      .page-header__toggle {
        background-image: url("../img/close.svg");
        background-size: 18px 18px;
      }

      .page-header__extra-nav {
        display: flex;
      }

      .page-header__store {
        display: block;
      }

      .page-header__main-nav {
        display: block;
      }

      .page-header__lang-switcher {
        display: block;
      }
    }
  }
}

.page-header__row {
  display: flex;
  flex: initial;

  @media (max-width: 992px) {
    height: 100%;
  }

  &_border-bottom {
    padding-bottom: 15px;
    border-bottom: 1px solid #00091c;

    @media (max-width: 992px) {
      border-bottom: none;
      padding-bottom: 0;
    }
  }
}

.page-header__logo {
  display: flex;
  align-items: center;
  margin-right: auto;
  flex: auto;
  height: auto;
  padding-right: 30px;

  @media (max-width: 992px) {
    flex: none;
    width: auto;
    height: 30px;
    margin-top: 10px;
    margin-bottom: 10px;
  }

  img {
    max-width: 100%;
    display: block;
    height: 100%;
  }
}

.page-header__contacts {
  margin-right: 60px;
  margin-left: auto;

  @media (max-width: 992px) {
    display: none;
  }
}

.page-header__inner {
  flex: initial;
}

.page-header__store {
  box-sizing: border-box;
  padding: 20px 0 20px 60px;
  margin-left: auto;

  @media (max-width: 992px) {
    display: none;
    position: absolute;
    padding-top: 0;
    padding-bottom: 40px;
    top: 305px;
    right: 0;
    left: 0;
    padding-left: 0;
    background-color: #ffffff;
    text-align: center;
  }
}

.page-header__button {
  color: #00091c;
  font-size: 20px;
  font-weight: 400;
  line-height: 36px;
  text-decoration: none;
  white-space: nowrap;

  @media (max-width: 1924px) {
    font-size: 18px;
  }

  @media (max-width: 992px) {
    color: #ffffff;
    background-color: #306ae0;
    padding: 13px 40px;
  }

  @media (min-width: 993px) {
    &:hover,
    &:focus {
      color: #306ae0;
    }

    &:active {
      opacity: 0.7;
    }
  }
}

.page-header__arrow {
  margin-left: 12px;

  @media (max-width: 992px) {
    background-position: -36px 0;
  }
}

.page-header__toggle {
  display: none;

  @media (max-width: 992px) {
    display: block;
    position: absolute;
    top: 5px;
    right: 25px;
    width: 30px;
    height: 40px;
    font-size: 0;
    border: none;
    background-image: url("../img/menu-icon.svg");
    background-repeat: no-repeat;
    background-position: right center;
    outline: none;
  }
}

.page-header__lang-switcher {
  @media (max-width: 992px) {
    display: none;
    position: absolute;
    padding-bottom: 20px;
    top: 725px;
    left: 0;
    right: 0;
    justify-content: center;
    background-color: #ffffff;
  }
}

.page-header__extra-nav {
  @media (max-width: 992px) {
    display: none;
  }
}

.page-header__main-nav {
  @media (max-width: 992px) {
    display: none;
  }
}

