.page-footer__container {
  position: relative;
  display: flex;
  flex-direction: column;
  font-size: 14px;
  color: @text-color_default;
  line-height: 30px;
  letter-spacing: 0.35px;
  z-index: 10;
}

.page-footer__inner {
  display: flex;

  &:first-child {
    padding-bottom: 34px;
    border-bottom: @border-default;
  }
}

.page-footer__column {
  width: 170px;

  &_type_social {
    display: flex;
    flex-direction: column;
    margin-left: auto;
    align-items: flex-end;
  }

  &:not(:last-child) {
    margin-right: 8.5%;
  }
}

.page-footer__title {
  margin-top: 0;
  margin-bottom: 6px;
  font-weight: 500;
}

.page-footer____description {
  margin-top: 0;
  margin-bottom: 0;
}

.page-footer__list {
  display: flex;
  justify-content: space-between;
  width: 110px;
  padding-left: 0;
  list-style: none;
}

.page-footer__link {
  font-size: 0;
  letter-spacing: 0;
}

.page-footer__icon {
  display: inline-block;
  width: 32px;
  height: 32px;
  background-image: url("../img/sprites-socials.png");
  background-repeat: no-repeat;

  &_facebook {
    background-position: 0 0;
  }

  &_instagram {
    background-position: 0 -49px;
  }
}

.page-footer__feedback {
  margin-right: auto;
}

@media screen and (min-width: 1200px) {
  .page-footer__icon {
    &_facebook {
      &:hover,
      &:focus {
        background-position: -50px 0;
      }
    }

    &_instagram {
      &:hover,
      &:focus {
        background-position: -50px -50px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .page-footer__column:not(:last-child) {
    margin-right: 4%;
  }
}

@media screen and (max-width: 1024px) {
  .page-footer__column:not(:last-child) {
    margin-right: 3%;
  }
}

@media screen and (max-width: 660px) {
  .page-footer__inner {
    flex-direction: column;
  }

  .page-footer__column_type_social {
    margin-left: 0;
    align-items: flex-start;
  }

  .page-footer__column,
  .work-time {
    margin-bottom: 10px;
  }

  .page-footer__list {
    width: 90px;
    margin-top: 10px;
  }

  .page-footer__icon {
    transform: scale(0.9);
  }

  .page-footer__title {
    margin-bottom: 0;
  }

  .page-footer__inner:first-child {
    padding-bottom: 0;
    border-bottom: none;
  }

  .page-footer__container_index {
    margin-top: 200px;
  }
}
