.section-title {
  color: @text-color_default;
  font-size: 48px;
  font-weight: 500;
  line-height: 58px;
  letter-spacing: 1.2px;
}

@media screen and (max-width: 1024px) {
  .section-title {
    font-size: 32px;
    line-height: 48px;
  }
}

@media screen and (max-width: 660px) {
  .section-title {
    font-size: 28px;
    line-height: 42px;
  }
}

