.summary {
  display: flex;
  margin-top: 20px;
  justify-content: flex-end;
}

.summary__price {
  margin-right: 60px;
  line-height: 70px;
  font-weight: 500;
  font-size: @text-size_huge;
  color: @text-color_accent;
}

.summary__submit {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 148px;
  height: 70px;
  box-sizing: border-box;
  background-color: @bg-color_accent;
  border: 1px solid @bg-color_accent;
  color: @text-color_light;
  font-size: @text-size_default;
  cursor: pointer;

  .font-family();

  &:hover,
  &:focus {
    color: @text-color_default;
    background-color: transparent;
    border-color: @border-color-dark;
  }
}

@media screen and (max-width: 768px) {
  .summary {
    margin-top: 0;
    margin-bottom: 0;
  }

  .summary__submit {
    //font-size: 20px;
    line-height: 30px;
    width: 120px;
    height: 52px;
    text-align: center;
  }

  .summary__price {
    font-size: 40px;
    line-height: 52px;
  }
}
