.font-family() {
  font-family: "GTWalsheimPro", "Arial", sans-serif;
}

.container() {
  margin-right: auto;
  margin-left: auto;
  padding-left: 20px;
  padding-right: 20px;
  max-width: 1410px;
}

.img() {
  max-width: 100%;
  height: auto;
}

/// Примесь используется внутри целевого селектора. На выходе получаем отдельный медиа-запрос
.responsive(@maxWidth; @rules) {
  @media only screen and (max-width: @maxWidth) {
    @rules();
  }
}

.responsive-not-touch (@minWidth; @rules) {
  @media only screen and (min-width: @minWidth) {
    @rules();
  }
}

.border-top-bottom() {
  border-top: @border-default;
  border-bottom: @border-default;
}

.control-state (@color-text, @color-bg, @percent) {
  &:hover,
  &:focus {
    color: @color-text;
  }

  &:active {
    background-color: @color-bg, @percent + "%";
  }
}

.header-height (@height: 70px) {
  height: @height;
}
