.offer__table {
  .container();
}

.offer__lead {
  font-size: @text-size_huge;
  color: @text-color_default;
  font-weight: 500;
  letter-spacing: 1.2px;
  text-align: center;

  .container();
}

.offer__highlighting {
  color: @text-color_accent;
}

.offer__wrapper {
  box-sizing: border-box;
  overflow-x: auto;
  overflow-y: hidden;
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
  padding-bottom: 40px;
}

@media screen and (max-width: 1200px) {
  .offer__lead {
    font-size: 40px;
    margin-bottom: 48px;
  }
}

@media screen and (max-width: 1024px) {
  .offer__lead {
    font-size: 32px;
    line-height: 38px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (max-width: 660px) {
  .offer__lead {
    font-size: 28px;
    line-height: 42px;
  }
}
