.lead {
  margin-top: 13px;
  font-size: 30px;
  font-weight: 500;
  line-height: 40px;
  letter-spacing: 0.75px;
  color: @text-color_accent;
  max-width: 570px;
}

@media screen and (max-width: 1024px) {
  .lead {
    font-size: 18px;
    line-height: 36px;
  }
}

@media screen and (max-width: 768px) {
  .lead {
    max-width: none;
  }
}

@media screen and (max-width: 660px) {
  .lead {
    font-size: 16px;
    line-height: 32px;
  }
}
