.payment {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.payment__button {
  margin-bottom: 50px;

  &_active {
    .button__arrow {
      transform: rotate(90deg);
      transition: transform 0.15s ease-in;
    }
  }
}

.payment__card {
  box-sizing: border-box;
  padding: 56px 70px 70px;
  display: none;
  width: 100%;
  background-color: @bg-color_light;
  border: 1px solid @border-color-grey;
}

.payment__switch {
  margin-bottom: 50px;
}

.payment__wrapper {
  max-width: 1440px;
  align-self: center;
  margin-left: auto;
  margin-right: auto;
}

.payment__subscription {
  display: none;
}

.payment__datepicker {
  position: relative;
  padding-right: 120px;

  &.error {
    .datepicker {
      border-color: @red;
    }

    &::after {
      content: "Выберите дату";
      color: @red;
      font-size: 13px;
      line-height: 19px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .payment__datepicker {
    margin-bottom: 40px;
    padding-right: 0;
  }

  .payment__card {
    border: none;
  }
}

@media screen and (max-width: 768px) {
  .payment__wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .payment__card {
    border: none;
    padding: 0;
  }

  .payment__switch {
    margin-bottom: 32px;
  }
}

@media screen and (max-width: 660px) {
  .payment__wrapper {
    padding-left: 20px;
    padding-right: 20px;
    margin-left: 0;
    margin-right: 0;
    max-width: none;
  }

  .payment__datepicker {
    margin-bottom: 32px;
  }
}
