.subscription__summary {
  margin-left: auto;
}

.subscription__wrapper {
  display: flex;
  flex-direction: column;
}

.subscription__inner {
  display: flex;
  flex-direction: column;
  width: 550px;
}

.subscription__item {
  margin-bottom: 42px;
}

@media screen and (max-width: 768px) {
  .subscription__item {
    flex-direction: column;
    align-items: flex-start;

    &:last-child {
      flex-direction: row;
    }
  }

  .subscription__inner {
    width: 100%;
  }
}
