.extra-nav {
  display: flex;
  flex: initial;
  justify-content: space-between;
  align-items: center;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
  width: 1000px;

  @media (max-width: 1250px) {
    max-width: 900px;
    width: 900px;
  }

  @media (max-width: 992px) {
    position: absolute;
    top: 50px;
    right: 0;
    left: 0;
    padding-bottom: 40px;
    max-width: none;
    width: auto;
    flex-direction: column;
    background-color: #ffffff;
  }
}

.extra-nav__link {
  font-size: 20px;
  font-weight: 400;
  line-height: 36px;
  letter-spacing: 0.5px;
  color: #306ae0;
  text-decoration: none;

  @media (max-width: 1024px) {
    font-size: 18px;
  }

  @media (max-width: 992px) {
    color: #00091c;

    &:focus,
    &:hover {
      color: #306ae0;
    }
    &:active {
      opacity: 0.7;
    }
  }

  @media (min-width: 993px) {
    &:hover,
    &:focus {
      color: #00091c;
    }

    &:active {
      opacity: 0.7;
    }
  }
}


