.ticket {
  display: flex;
  justify-content: space-between;
  font-size: @text-size_default;

  .font-family();
}

.ticket__item {
  display: flex;
  align-items: center;
  margin-bottom: 28px;
}

.ticket__inner {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  width: 548px;
}

@media screen and (max-width: 1200px) {
  .ticket {
    flex-direction: column;
    align-items: center;
  }

  .datepicker-inline .datepicker {
    margin-right: 0;
  }
}

@media screen and (max-width: 768px) {
  .ticket__item {
    flex-direction: column;
    align-items: flex-start;

    &:last-child {
      flex-direction: row;
    }
  }

  .ticket__inner {
    width: 100%;
  }
}
