.index-sales {
  box-sizing: border-box;
  margin-top: 80px;

  .container();
}

.index-sales__section-title {
  margin-top: 50px;
  margin-bottom: 30px;
}

.index-sales__text {
  max-width: 340px;
  margin-bottom: 95px;
}

.index-sales__article {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-left: calc(17% + 15px);
  flex-basis: 50%;
}

@media screen and (max-width: 1200px) {
  .index-sales {
    margin-top: 60px;
  }

  .index-sales__article {
    margin-left: 4%;
    padding-left: 0;
  }
}

@media screen and (max-width: 1024px) {
  .index-sales__text {
    margin-bottom: 56px;
  }
}

@media screen and (max-width: 768px) {
  .index-sales__button {
    margin-bottom: 54px;
  }
}

@media screen and (max-width: 660px) {
  .index-sales {
    margin-top: 19px;
  }

  .index-sales__article {
    margin-left: 0;
  }

  .index-sales__section-title {
    margin-top: 18px;
    margin-bottom: 10px;
  }

  .index-sales__text {
    margin-bottom: 30px;
  }
}
