.text {
  position: relative;
  margin-bottom: 35px;
  font-size: 20px;
  font-weight: 400;
  line-height: 36px;
  letter-spacing: 0.5px;
  color: @text-color_default;
  max-width: 570px;
  z-index: 20;
}

@media screen and (max-width: 1024px) {
  .text {
    font-size: 14px;
    line-height: 30px;
  }
}

@media screen and (max-width: 768px) {
  .text {
    max-width: none;
  }
}
