.result {
  display: none;
  color: @text-color_default;
}

.result__wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.result__header {
  margin-top: 0;
  margin-bottom: 35px;
  font-size: @text-size_big;
  font-weight: 500;
}

.result__body {
  display: flex;
  flex-wrap: wrap;
}

.result__item {
  flex-basis: 50%;
  padding-top: 30px;
  padding-bottom: 30px;
  font-size: @text-size_default;
  line-height: 36px;
}

.result__title {
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
  width: 145px;
}

.result__footer {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 54px;
}

.result__info {
  margin: 0;
  max-width: 580px;
  font-size: @text-size_default;
}

.result__return {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 148px;
  height: 70px;
  box-sizing: border-box;
  background-color: @bg-color_accent;
  border: 1px solid @bg-color_accent;
  color: @text-color_light;
  font-size: @text-size_default;
  cursor: pointer;

  .font-family();

  &:hover,
  &:focus {
    color: @text-color_default;
    background-color: transparent;
    border-color: @border-color-dark;
  }
}

@media screen and (max-width: 1200px) {
  .result__body {
    flex-direction: column;
    align-items: baseline;
  }

  .result__footer {
    flex-direction: column;
    align-items: center;
  }

  .result__info {
    text-align: center;
    margin-bottom: 30px;
  }
}
