.lang-switcher {
  position: relative;
  display: flex;
  font-size: 14px;
  font-weight: 400;
  line-height: 36px;
  letter-spacing: 0.35px;
  color: #00091c;
}

.lang-switcher__button {
  border: none;
  background-color: transparent;
  display: flex;
  align-items: center;
  cursor: pointer;
  outline: none;
  font-size: 14px;
  padding-right: 0;
  padding-left: 0;
  margin-right: -5px;
  height: 100%;

  .font-family();

  @media (max-width: 992px) {
    margin-right: 8px;
    margin-left: 8px;
    padding-right: 20px;
    padding-left: 20px;

    svg {
      display: none;
    }
  }

  @media (min-width: 993px) {
    &:hover,
    &:focus {
      color: #306ae0;
    }

    &:active {
      opacity: 0.7;
    }
  }

  svg {
    margin-left: 8px;
    transform: rotate(0);
    transition: transform 0.2s cubic-bezier(0.4, 0, 0.2, 1);
    transform-origin: 40% 50%;
  }

  &.active {
    svg {
      transform: rotate(90deg);
      transition: transform 0.25s cubic-bezier(0.4, 0, 0.2, 1);
    }
  }
}

.lang-switcher__list {
  position: absolute;
  margin: 0;
  top: 100%;
  left: 0;
  background-color: #f4fdff;
  border: 1px solid #dfe3ea;
  padding: 8px 16px;
  list-style: none;
  display: none;
  z-index: 10;

  @media (max-width: 992px) {
    position: static;
    display: flex;
    background-color: #ffffff;
    border: none;
    padding: 0;
  }
}

.lang-switcher__item {
  @media (max-width: 992px) {
    margin-right: 8px;
    margin-left: 8px;
    padding-right: 20px;
    padding-left: 20px;

    &.active {
      background-color: #306ae0;
      color: #ffffff;

      .lang-switcher__link {
        color: #ffffff;
      }
    }
  }
}

.lang-switcher__link {
  color: #00091c;
  text-decoration: none;

  @media (min-width: 993px) {
    &:hover,
    &:focus {
      color: #306ae0;
    }

    &:active {
      opacity: 0.7;
    }
  }
}

.lang-switcher__wrapper {
  @media (max-width: 992px) {
    display: flex;
  }
}

.lang-switcher__button.active {
  @media (max-width: 992px) {
    background-color: #306ae0;
    color: #ffffff;
  }
}

