// Переопределяет стандартные стили плагина
.datepicker {
  width: 390px;
  background-color: transparent;
  font-size: @text-size_default;
  border-radius: 0;

  .font-family();
}

.datepicker--cell {
  height: 48px;
  border-radius: 0;
  border-top: 3px solid @border-color-light;
  border-right: 5px solid @border-color-light;
  border-bottom: 3px solid @border-color-light;
  border-left: 5px solid @border-color-light;
}

.datepicker--day-name {
  color: @text-color_default;
  text-transform: none;
}

.datepicker--cell.-current- {
  color: @text-color_green;
}

.datepicker--cell.-selected-,
.datepicker--cell.-selected-.-current-,
.datepicker--cell.-selected-.-focus- {
  background-color: @bg-color_green;
  border-top: 3px solid @border-color-light;
  border-right: 5px solid @border-color-light;
  border-bottom: 3px solid @border-color-light;
  border-left: 5px solid @border-color-light;
}

.datepicker--days-names {
  margin: 4px 0 12px;
}

.datepicker--nav-title i {
  color: @text-color_default;
}

.datepicker--nav {
  padding: 14px;
  border-bottom: none;
}

.datepicker--content {
  padding: 4px 10px;
}

.datepicker--cell-month {
  width: 25%;
}

@media screen and (max-width: 768px) {
  .datepicker {
    width: 100%;
  }
}
