.table {
  table-layout: fixed;
  border-collapse: collapse;
  font-size: @text-size_default;
  font-weight: 400;
  text-align: center;
}

.table__cell {
  padding: 20px 30px;
  border-right: @border-table;

  &_type_header {
    font-size: @text-size_default;
    font-weight: 400;
  }

  &_first-column {
    text-align: left;
    white-space: nowrap;
  }

  &_decor-border {
    position: relative;

    &::before,
    &::after {
      content: "";
      position: absolute;
      background-color: @border-color-grey;
    }

    &_bottom {
      &::after {
        bottom: -30px;
        right: -1px;
        width: 1px;
        height: 30px;
      }
    }

    &_right {
      &::before {
        bottom: -1px;
        right: -30px;
        width: 30px;
        height: 1px;
      }
    }
  }
}

.table__row {
  border-bottom: @border-table;
}

@media screen and (max-width: 1024px) {
  .table,
  .table__cell_type_header {
    font-size: 16px;
  }

  .table__cell {
    padding: 14px 28px;
  }
}

@media screen and (max-width: 660px) {
  .table,
  .table__cell_type_header {
    font-size: 16px;
  }

  .table__cell {
    padding: 14px 20px;
  }
}
