.switch {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.switch__subtitle {
  display: inline-flex;
  font-size: @text-size_big;
  line-height: 38px;

  &_before {
    order: 1;
  }

  &_after {
    order: 3;
  }
}

.switch__label {
  order: 2;
  position: relative;
  display: inline-block;
  width: 76px;
  height: 38px;
  margin-left: 40px;
  margin-right: 40px;
  border: 1px solid @border-color-grey;
  transition: 0.3s;
}


.switch__slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: @bg-color_light;
  transition: 0.3s;

  &::before {
    content: "";
    position: absolute;
    width: 38px;
    height: 38px;
    background-color: @bg-color_green;
    transition: transform 0.1s ease-in-out;
  }
}

.switch__checkbox {
  &:not(:checked) ~ .switch__subtitle_before,
  &:checked ~ .switch__subtitle_after {
    color: @text-color_green;
  }

  &:checked ~ .switch__label > .switch__slider::before {
    transform: translateX(38px);
  }
}

@media screen and (max-width: 1200px) {
  .switch {
    justify-content: center;
  }
}

@media screen and (max-width: 1024px) {
  .switch__subtitle {
    font-size: 20px;
  }
}

@media screen and (max-width: 768px) {
  .switch__label {
    margin-left: 30px;
    margin-right: 30px;
    width: 64px;
    height: 32px;
  }

  .switch__subtitle {
    font-size: 18px;
    justify-content: flex-end;
  }

  .switch__slider::before {
    width: 32px;
    height: 32px;
  }

  .switch__checkbox:checked ~ .switch__label > .switch__slider::before {
    transform: translateX(32px);
  }
}
