.row {
  display: flex;
  box-sizing: border-box;

  &_margin-bottom {
    &_medium {
      margin-bottom: 130px;
    }

    &_large {
      margin-bottom: 350px;
    }
  }

  &_container {
    width: 100%;
    max-width: 1410px;
    align-self: center;
  }
}

@media screen and (max-width: 1024px) {
  .row_margin-bottom_large {
    margin-bottom: 230px;
  }

}

@media screen and (max-width: 768px) {
  .row {
    flex-direction: column;
  }

  .row_margin-bottom_large {
    margin-bottom: 100px;
  }
}

@media screen and (max-width: 660px) {
  .row {
    flex-direction: column;
    margin-left: 0;
    margin-right: 0;
  }
}
