.contacts {
  display: flex;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
  font-size: 14px;
  font-weight: 400;
  line-height: 36px;
  letter-spacing: 0.35px;
  color: #00091c;
}

.contacts__item {
  &:not(:last-child) {
    margin-right: 30px;
  }
}

.contacts__link {
  color: #00091c;
  text-decoration: none;

  @media (min-width: 993px) {
    &:hover,
    &:focus {
      color: #306ae0;
      text-decoration: underline;
    }

    &:active {
      opacity: 0.7;
    }
  }
}

