.input {
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  &.error {
    .input__control,
    .input__label,
    .selectric {
      border-color: @border-color-red;
      background-color: rgba(@bg-color_red, 0.05);
    }

    .input__warning {
      display: block;
    }
  }
}

.input__inner {
  display: flex;
  justify-content: space-between;
  width: 448px;
}

.input__label {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 132px;
  height: 58px;
  background-color: @bg-color_light;
  border: 1px solid @border-color-grey;
  cursor: pointer;

  &:hover,
  &:focus {
    color: @text-color_light;
    background-color: @bg-color_green;
    border-color: @border-color-green;
  }

  &:active {
    background-color: darken(@bg-color_green, 10%);
    border-color: darken(@border-color-green, 10%);
  }
}

.input__title {
  display: inline-flex;
  margin-right: auto;
  cursor: pointer;
}

.input__radio:checked + .input__label {
  color: @text-color_light;
  background-color: @bg-color_green;
  border-color: @border-color-green;
}

.input__control {
  display: flex;
  align-items: center;
  width: 448px;
  height: 60px;
  padding-left: 25px;
  padding-right: 25px;
  box-sizing: border-box;
  background-color: transparent;
  border: 1px solid @border-color-grey;
  font-size: @text-size_default;

  .font-family();

  &:focus {
    outline: none;
    border-color: @border-color-dark;
  }
}

.input__warning {
  display: none;
  margin-top: 4px;
  margin-left: 100px;
  font-size: 13px;
  line-height: 19px;
  color: @text-color_red;
}

@media screen and (max-width: 768px) {
  .input__title {
    margin-right: 0;
    margin-bottom: 16px;
  }

  .input__inner {
    font-size: 18px;
    width: 100%;
  }

  .input__label {
    width: 30%;
    font-size: 16px;
  }

  .input__control {
    width: 100%;
  }

  .input__warning {
    margin-left: 0;
  }
}
